import { CardSchema } from './../../models/CardSchema';
import { CardSrore } from './../../card-srore';
import { ListSchema } from './../../models/ListSchema';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-board-component',
  templateUrl: './board.component.html',
  styleUrls: ['./board.component.css']
})
export class BoardComponent implements OnInit {
  lists: Array<ListSchema> = [];
  cardStore: CardSrore;
  constructor() {}

  setMockData(): void {
    this.cardStore = new CardSrore();
    const lists: Array<ListSchema> = ['Winnie', 'Bob', 'Thomas', 'George']
      .map(name => {
        return <ListSchema>
          {
            title: name,
            cards: []
          };
      });
    lists[0].cards = ['Buy Milk', 'Return Text Book']
      .map(value => this.cardStore.newCard(value))
      .map(id => this.cardStore.getCard(id));
    this.lists = lists;
  }

  ngOnInit() {
    this.setMockData();
  }

}



